    z-index: 10;
  }
  .navbar__logo img{
    width: 70px;
    margin-top: 20px;
    
    
  }
  
  .navbar__nav {
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
  
  .navbar__nav a {
    color: black;
    text-decoration: none;
    font-size: 16px;
    font-weight: 300;
  }
  
  .navbar__button {
    background-color: rgb(124, 212, 242);
    color: white;
    border: none;
    border-radius: 4px;
