    font-size: 15px;
  }
 
  @media (max-width: 700px){
    .navbar__nav, .navbar__nav a, .navbar__button{
      display: none;
    }
    .navbar{
      height: 45px;
      padding: 1rem;

    }
     
 
  

  }

