}
.toggle-button{
  border: none;
  
 }

 .toggle-button .sp{
  height: 3px;
  width: 40px;
  background: #007bff;
  border-radius: 10px;
  transition: .3s ease;


 }

 .toggle-button .sp1{

  margin-bottom: 10px;
  margin-top: 10px;
   

}


.toggle-button .sp2{
   
  margin-top: 10px;
}



.toggle-button .sp3{
 
  margin-top: 10px;
}
.toggle-button.active .sp3{
  display: none;
}
.toggle-button.active .sp1{
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
